/* 0. Reset ================================= */

/* 
If you want to add partials, use Sass Modules via @use:
@use 'variables';

Usage: partial-name.variable/mixin/function-name:
background: variables.$primary-color;
*/

@use 'reset';
@import 'variables';
@import 'mixins';

/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background: $primary-color;
}

/* 2. Content ================================= */

body {
	font-family: $BodyFont;
	display: flex;
	flex-direction: column;
	padding: 45px 7%;

	.review_head{
		color: $textBLk;
		text-transform: capitalize;
		text-align: left;
		font-weight: 900;
		font-size: 24px;
		line-height: 130px;

		@include bp-L{
			text-align: center;
		}
	}
}

.review_summary{
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	grid-template-rows: repeat(2, 1fr), .5fr, .5fr;
	gap: 1em;
	max-height: 350px;

	.review_summary-options{
		grid-column: 1/5;
		height: 100%;
	}
	.review_summary-contact{
		grid-column: 2/5;
	}
	.review_summary-rating{
		display: flex;
		flex-direction: column;
		justify-content: end;
		padding-bottom: 5px;

		.review_summary-ratingContent{
			display: flex;
			flex-direction: column;

			.review_summary-ratingFlt{
				font-size: 37px;
				font-weight: bolder;
			}
			.review_summary-ratingStars{
				color: #ffab41;
				font-size: 21px;
			}
		}
		.review_summary-calc{
			font-size: 15px;
			margin-top: 20px;
			text-align: left;
		}
	}
	.review_summary-ranks{
		display: flex;
		flex-direction: column;
		padding: 5px 5%;
		opacity: 100%;
		border-left: solid .42px $border-color;
		border-right: solid .42px $border-color;
		margin-top: 20px;
		justify-content: space-between;

		.review_summary-rank{
			display: flex;
			justify-content: space-between;
			cursor: pointer;
			opacity: 100%;

			.rankStars{
				color: $star-color;
				font-size: 12px;
				display: flex;
				justify-content: space-between;
				width: 27%;
			}
			.rankBars{
				width: 130px;
				height: 15px;
				background-color: $bar-fill-color;
				border-radius: 6.66%;
				display: flex;
				justify-content: left;

				.rankBars_5{
					background-color: $bar-filled-color;
					width: 91%;
				}
				.rankBars_4{
					background-color: $bar-filled-color;
					width: 7%;
				}
				.rankBars_3{
					background-color: $bar-filled-color;
					width: 2%;
				}
			}
			.rankINT{
				text-align: left;
				width: 10%;
				color: #737978;
				font-size: 12px;
			}
		}
		.faded{
			opacity: 50%;
		}
	}
	.review_summary-images{
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-template-rows: repeat(2, 1fr);
		margin-top: 20px;

		figure{
			width: 80%;
			opacity: 100%;

			img{
				width: 100%;
			}
		}
		.faded{
			opacity: 50%;
		}
	}
	.review_summary-contact{
		grid-column: 1/6;
		display: flex;
		justify-content: center;
		gap: 1em;
		margin-top: 20px;

		button{
			color: #000;
			text-align: center;
			border: 2px solid #212721;
			box-shadow: 2px 3px 5px 0 #8888886b;
			padding: 3px 20px;
			border-radius: 3px;
			font-weight: 700;
			font-size: 13px;
			line-height: 30px;
			background-color: $btn-fill-color;
			opacity: 100%;
		}
		button:hover{
			box-shadow: none;
			opacity: 80%;
		}
	}
	@include bp-M{
		grid-template-columns: repeat(8, 1fr);
		grid-template-rows: repeat(2, 1fr);

		.review_summary-rating{
			grid-area: 1/1/2/4;
			width: 100%;
		}
		.review_summary-ranks{
			grid-area: 1/4/2/9;
		}
		.review_summary-images{
			grid-area: 2/1/3/5;
		}
		.review_summary-contact{
			grid-area: 2/5/3/9;
			align-items: center;

			button{
				height: min-content;
				padding: 2px 4px;
			}
		}
	}
	@include bp-L{
		grid-template-columns: repeat(5, 1fr);
		grid-template-rows: 1fr, .5fr;

		.review_summary-rating{
			grid-area: 1/1/2/2;
			width: 100%;
			padding-bottom: 20px;
		}
		.review_summary-ranks{
			grid-area: 1/2/2/4;
		}
		.review_summary-images{
			grid-area: 1/4/2/7;
		}
		.review_summary-contact{
			grid-area: 2/1/3/7;
			align-items: center;

			button{
				height: min-content;
				padding: 3px 20px;
			}
		}
	}
	@include bp-XL{
		grid-template-columns: repeat(7, 1fr);
		grid-template-rows: 1fr;

		.review_summary-rating{
			grid-area: 1/1/2/2;
			width: 100%;
		}
		.review_summary-ranks{
			grid-area: 1/2/2/4;
		}
		.review_summary-images{
			grid-area: 1/4/2/6;
		}
		.review_summary-contact{
			grid-area: 1/6/2/8;
			align-items: center;

			button{
				height: min-content;
				padding: 3px 20px;
			}
		}
	}
}
